















import { Component, PropSync, Prop, Emit, Vue } from 'vue-property-decorator'

@Component({
  model: {
    prop: 'value',
    event: 'update:value'
  }
})
export default class TextInput extends Vue {
  @PropSync('value', { type: String, default: '' })
  localValue!: string

  @Prop()
  placeholder!: string

  @Prop({ default: true })
  clearable!: boolean

  clearFilter() {
    this.localValue = ''
  }

  stop() {}

  focus() {
    this.$nextTick(() => {
      const input = this.$refs.input as HTMLInputElement
      input.focus()
    })
  }

  @Emit('enter')
  onEnter() {}
}
