










































































import { Component, Prop } from 'vue-property-decorator'
import Grid from './Grid.vue'

@Component({
  name: 'VCards'
})
export default class VCards extends Grid {
  @Prop({ default: 2 })
  colMd!: number

  @Prop({ default: 3 })
  colLg!: number

  @Prop({ default: 4 })
  colXl!: number

  get columnClasses() {
    return [
      `vgrid-col-md-${this.colMd}`,
      `vgrid-col-lg-${this.colLg}`,
      `vgrid-col-xl-${this.colXl}`
    ]
  }
}
